<template>
  <div
    class="word-container"
    :style="{
      margin: `0 ${margin}rem 0 ${margin}rem`,
    }"
  >
    <div
      ref="fakeEl"
      class="position-absolute"
      :style="{ visibility: 'hidden' }"
    >
      {{ form.text }}
    </div>
    <div v-if="word.chord" class="word-chord">
      <div class="position-relative">
        <chord
          :chord="word.chord"
          :chordOffset="word.offset"
          :showText="!showChordForm"
          v-on:updateOffset="(offset: number) => onUpdateOffset(offset)"
        ></chord>
      </div>
    </div>
    <div class="position-relative">
      <chord-form
        v-if="showChordForm"
        :chord="word.chord"
        :textWidth="textWidth"
        v-on:onClose="(value: string) => onUpdateChord(value)"
      ></chord-form>
    </div>
    <div ref="wordEl">
      <div v-if="showWordTextForm">
        <input
          :style="{ width: `${inputWidth}px` }"
          ref="wordTextInputEl"
          type="text"
          v-model="form.text"
          @keyup.enter="handleEnter"
          @keyup.esc="handleEsc"
          @keydown.space="(e: KeyboardEvent) => e.preventDefault()"
        />
      </div>
      <template v-else>
        <div
          v-if="!isEmpty(word.text)"
          @click="onClickWord"
          @mouseenter="onMouseEnter"
          @mouseleave="onMouseLeave"
          :class="{
            'word-over': isMouseOverWord,
            'word-cursor': isMouseOverWord && !editorMenuService.isSelected(),
          }"
        >
          {{ word.text }}
        </div>
        <div v-else class="empty-word position-relative">
          <div
            @click="onClickWord"
            @mouseenter="onMouseEnter"
            @mouseleave="onMouseLeave"
            class="position-absolute"
            :class="{
              'word-over': isMouseOverWord,
              'word-cursor': isMouseOverWord && !editorMenuService.isSelected(),
            }"
          ></div>
        </div>
      </template>
    </div>
  </div>
</template>

<script setup lang="ts">
import {inject, onBeforeUnmount, onMounted, reactive, Reactive, Ref, ref, watch} from "vue";
import { WordInterface } from "@/interfaces/interfaces";
import Chord from "@/components/chord.component.vue";
import ChordForm from "@/components/chord-form.component.vue";
import { EditorMenuService } from "@/services/editor-menu.service";
import { isEmpty } from "@/utils/utils";
import { ChordFormService } from "@/services/chord-form.service";
import { ScrollServiceState, ScrollService } from "@/services/scroll.service";

interface WordProps {
  word: WordInterface;
}

const chordFormService: ChordFormService = inject("chordFormService")!;
const editorMenuService: EditorMenuService = inject("editorMenuService")!;
const scrollService: ScrollService = inject("scrollService")!;

const props: WordProps = defineProps<WordProps>();

const emitter = defineEmits<{
  (e: "onUpdate", word: WordInterface): void;
  (e: "onDelete"): void;
}>();

const wordEl: Ref<HTMLElement | null> = ref(null);
const wordTextInputEl: Ref<HTMLElement | null> = ref(null);
const fakeEl: Ref<HTMLElement | null> = ref(null);

const inputWidth: Ref<number> = ref(1);
const isMouseOverWord: Ref<boolean> = ref(false);
const showChordForm: Ref<boolean> = ref(false);
const showWordTextForm: Ref<boolean> = ref(false);
const textWidth: Ref<number> = ref(0);
const scrollDirection: Reactive<ScrollServiceState> = scrollService.state;

const DEFAULT_MARGIN_VALUE = 0.25;
const margin: Ref<number> = ref(DEFAULT_MARGIN_VALUE);

const form: Reactive<{ text: string | undefined }> = reactive({
  text: undefined,
});

watch(chordFormService.hide, (value) => {
  showChordForm.value = false;
});

watch(wordTextInputEl, (value) => {
  if (value != null) {
    value.focus();
    // value.setSelectionRange(0, 0);
  } else {
    if (form.text === undefined || isEmpty(form.text)) {
      emitter("onDelete");
    } else {
      if (props.word.text !== form.text) {
        const newWord: WordInterface = {
          ...props.word,
          text: form.text,
        };
        emitter("onUpdate", newWord);
      }
    }
  }
});

watch(scrollDirection, (value) => {
  if (editorMenuService.isSpacingWordSelected() && isMouseOverWord.value) {
    const sum: number = value.scrollDirection === "up" ? 0.1 : -0.1;
    margin.value += sum;
    if (margin.value < DEFAULT_MARGIN_VALUE) {
      margin.value = DEFAULT_MARGIN_VALUE;
    }
    onUpdateMargin(margin.value);
    value.event?.preventDefault();
  }
});

watch(
  () => props.word,
  (value) => {
    margin.value = value.margin ?? DEFAULT_MARGIN_VALUE;
  },
  { immediate: true }
);

const resizeObserver: ResizeObserver = new ResizeObserver((div) => {
  const width: number = div[0].target.getBoundingClientRect().width;
  inputWidth.value = width === 0 ? 1 : width;
});

onMounted(() => {
  resizeObserver.observe(fakeEl.value!);
  form.text = props.word.text;
  if (wordEl.value != null) {
    textWidth.value = wordEl.value.getBoundingClientRect().width;
  }
});

onBeforeUnmount(() => {
  resizeObserver.disconnect();
})

function onClickWord(): void {
  if (!editorMenuService.isSelected()) {
    if (!showChordForm.value) {
      setTimeout(() => {
        showChordForm.value = true;
      });
    }
  } else if (editorMenuService.isEraseWordSelected()) {
    emitter("onDelete");
  } else if (editorMenuService.isEditWordTextSelected()) {
    showWordTextForm.value = true;
  }
}

function onUpdateChord(chord: string): void {
  const newWord: WordInterface = {
    ...props.word,
    chord: chord,
  };
  emitter("onUpdate", newWord);
}

function onUpdateOffset(offset: number): void {
  const newWord: WordInterface = {
    ...props.word,
    offset: offset,
  };
  emitter("onUpdate", newWord);
}

function onUpdateMargin(margin: number): void {
  const newWord: WordInterface = {
    ...props.word,
    margin: margin,
  };
  emitter("onUpdate", newWord);
}

function onMouseEnter(): void {
  isMouseOverWord.value = true;
}

function onMouseLeave(): void {
  isMouseOverWord.value = false;
}

function handleEnter(): void {
  showWordTextForm.value = false;
}
function handleEsc(): void {
  form.text = props.word.text;
  showWordTextForm.value = false;
}
</script>

<style scoped lang="scss">
.word-container {
  display: flex;
  flex-direction: column;
  justify-content: end;

  .word-chord {
    height: 1rem;
  }

  .empty-word {
    width: 1rem;
    height: 1.5rem;
  }

  .empty-word > * {
    width: 1rem;
    height: 1rem;
    border: 1px dashed var(--bs-gray-300);
    border-radius: 0.25rem;
    top: 0.3rem;
  }
}

input[type="text"] {
  all: unset;
  text-align: start;
}
input[type="text"]:focus-visible {
  outline: none;
}

.word-cursor {
  cursor: pointer;
}

.word-over {
  box-shadow: 0px 0px 13px -2px rgba(0, 0, 0, 0.32);
  box-shadow: inset 0px 0px 23px -5px rgba(0, 0, 0, 0.48);
  border-radius: 5px;
}
</style>
