import { createApp } from "vue";
import App from "./App.vue";
import "./registerServiceWorker";
import router from "./router";
import store from "./store";
import "bootstrap/dist/css/bootstrap.min.css";
import "bootstrap";
import { ChordFormService } from "@/services/chord-form.service";
import { EditorMenuService } from "@/services/editor-menu.service";
import { AppStateService } from "@/services/appStateService";
import { ScrollService } from "@/services/scroll.service";
import { createI18n } from "vue-i18n";
import en from "@/localizations/en.json";

const i18n = createI18n({
  locale: "en",
  fallbackLocale: "en",
  messages: {
    en: en,
  },
});

const app = createApp(App);

app.use(store).use(router).use(i18n).mount("#app");

app.provide("appStateService", new AppStateService());
app.provide("chordFormService", new ChordFormService());
app.provide("editorMenuService", new EditorMenuService());
app.provide("scrollService", new ScrollService());
