<template>
  <div ref="el" class="icon-container">
    <div
      class="icon-wrapper"
      :title="_props.tooltip"
      @mouseenter="isOver = true"
      @mouseleave="isOver = false"
      @mousedown="onMouseClickDown"
      @mouseup="onMouseClickUp"
      @click="onClick()"
      :style="{
        padding: `${_props.style?.padding}rem`,
      }"
    >
      <div
        class="icon"
        :class="{
          'icon-click': !_props.disable && (isClick || _props.selected),
          'icon-border': _props.style?.border,
          'icon-disable': _props.disable,
        }"
        :style="{
          width: `${_props.style?.width}px`,
          height: `${_props.style?.height}px`,
          'background-image': `url(${require('@/icons/' + _props.icon)})`,
        }"
      ></div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { onMounted, reactive, Reactive, ref, Ref, watch } from "vue";

interface Style {
  height?: number; //px
  width?: number; //px
  border?: boolean;
  padding?: number; //rem
  margin?: number; //rem
}

interface Props {
  icon?: string;
  disable?: boolean;
  selected?: boolean;
  tooltip?: string;
  style?: Style;
}

const el: Ref<HTMLElement | undefined> = ref();
const isOver: Ref<boolean> = ref(false);
const isClick: Ref<boolean> = ref(false);

function onMouseClickDown(): void {
  isClick.value = true;
}
function onMouseClickUp(): void {
  isClick.value = false;
}

const props = defineProps<Props>();

const _props: Reactive<Props> = reactive({});

// default values
watch(
  () => props,
  (value) => {
    _props.icon = value.icon ?? "question.svg";
    _props.disable = value.disable ?? false;
    _props.selected = value.selected ?? false;
    _props.tooltip = value.tooltip ?? undefined;
    _props.style = {
      height: value.style?.height ?? 16,
      width: value.style?.width ?? 16,
      border: value.style?.border ?? false,
      padding: value.style?.padding ?? 0.25,
      margin: value.style?.margin ?? 0.25,
    };
  },
  { immediate: true, deep: true }
);

const emitter = defineEmits<{
  (e: "onClick"): void;
}>();

onMounted(() => {
  document.addEventListener("mouseup", handleClickOutside);
});

const handleClickOutside = (event: any) => {
  if (el.value && !el.value.contains(event.target)) {
    isClick.value = false;
  }
};

function onClick(): void {
  if (!props.disable) {
    emitter("onClick");
  }
}
</script>

<style scoped lang="scss">
.icon-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;

  .icon-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;

    .icon {
      cursor: pointer;
      background-size: cover;
    }
  }
}

.icon-border {
  border: var(--bs-border-width) var(--bs-border-style) var(--bs-border-color);
}

.icon-click {
  filter: drop-shadow(0.3px 1px 0.6px black);
}

.icon-disable {
  color: var(--bs-gray);
}
</style>
