import { reactive, Reactive } from "vue";

export interface EditorMenuAction {
  selected: boolean;
}

export interface EditorMenuActions {
  eraseWord: EditorMenuAction;
  editWordText: EditorMenuAction;
  addEmptyWord: EditorMenuAction;
  spacingWord: EditorMenuAction;
}

enum IconName {
  ERASE_WORD = "eraser-cursor",
  EDIT_WORD_TEXT = "edit-word-text-cursor",
  ADD_EMPTY_WORD = "add-empty-word-cursor",
  SPACING_WORD = "spacing-word-cursor",
}

export class EditorMenuService {
  private classes: string[] = [
    IconName.ERASE_WORD,
    IconName.EDIT_WORD_TEXT,
    IconName.ADD_EMPTY_WORD,
    IconName.SPACING_WORD,
  ];

  public actions: Reactive<EditorMenuActions> = reactive({
    eraseWord: { selected: false },
    editWordText: { selected: false },
    addEmptyWord: { selected: false },
    spacingWord: { selected: false },
  });

  constructor() {
    // empty
  }

  public switchAllOff(): void {
    Object.values(this.actions).forEach(
      (i: EditorMenuAction) => (i.selected = false)
    );
    const editor: HTMLElement = document.getElementById("editor")!;
    this.classes.forEach((c: string) => {
      editor.classList.remove(c);
    });
  }

  public toggleEraseWord(): void {
    const editor: HTMLElement = document.getElementById("editor")!;
    const value: boolean = this.actions.eraseWord.selected;
    this.switchAllOff();
    this.actions.eraseWord.selected = !value;
    if (this.isEraseWordSelected()) {
      editor.classList.add(IconName.ERASE_WORD);
    }
  }

  public toggleEditWordText(): void {
    const editor: HTMLElement = document.getElementById("editor")!;
    const value: boolean = this.actions.editWordText.selected;
    this.switchAllOff();
    this.actions.editWordText.selected = !value;
    if (this.isEditWordTextSelected()) {
      editor.classList.add(IconName.EDIT_WORD_TEXT);
    }
  }

  public toggleAddEmptyWord(): void {
    const editor: HTMLElement = document.getElementById("editor")!;
    const value: boolean = this.actions.addEmptyWord.selected;
    this.switchAllOff();
    this.actions.addEmptyWord.selected = !value;
    if (this.isAddEmptyWordSelected()) {
      editor.classList.add(IconName.ADD_EMPTY_WORD);
    }
  }

  public toggleSpacingWord(): void {
    const editor: HTMLElement = document.getElementById("editor")!;
    const value: boolean = this.actions.spacingWord.selected;
    this.switchAllOff();
    this.actions.spacingWord.selected = !value;
    if (this.isSpacingWordSelected()) {
      editor.classList.add(IconName.SPACING_WORD);
    }
  }

  public isSelected(): boolean {
    return (
      this.actions.eraseWord.selected ||
      this.actions.editWordText.selected ||
      this.actions.addEmptyWord.selected ||
      this.actions.spacingWord.selected
    );
  }

  public isEraseWordSelected(): boolean {
    return this.actions.eraseWord.selected;
  }

  public isEditWordTextSelected(): boolean {
    return this.actions.editWordText.selected;
  }

  public isAddEmptyWordSelected(): boolean {
    return this.actions.addEmptyWord.selected;
  }

  public isSpacingWordSelected(): boolean {
    return this.actions.spacingWord.selected;
  }
}
