export function hashId(input: string): string {
  let hash = 0;
  const len = input.length; // Salviamo la lunghezza una sola volta
  for (let i = 0; i < len; i++) {
    const chr = input.charCodeAt(i);
    hash = (hash << 5) - hash + chr; // Calcolo dell'hash
    hash >>>= 0; // Convertiamo in un numero intero a 32 bit senza segno
  }
  return hash.toString(16);
}

export function isEmpty(str: string | undefined): boolean {
  if (str === undefined) return true;
  return str.trim().length === 0;
}

export function round(value: number, decimal: number): number {
  const factor: number = Math.pow(10, decimal);
  return Math.round(value * factor) / factor;
}

/**
 * Debounce Function
 *
 * This function limits the rate at which a given function (`func`) is executed.
 * The function will only be executed after a specified delay (`wait`) has passed
 * since the last invocation.
 *
 * @param func - The function to debounce.
 * @param wait - The number of milliseconds to wait before calling `func`.
 * @returns A debounced version of the input function.
 */
export function debounce<T extends (...args: any[]) => any>(func: T, wait: number): () => void {
  let timeout: ReturnType<typeof setTimeout> | undefined;

  return function (...args: any[]) {
    if (timeout) clearTimeout(timeout);

    timeout = setTimeout(() => func(...args), wait);
  };
}
