import { WordInterface } from "@/interfaces/interfaces";
import { isEmpty, round } from "@/utils/utils";

export type LyricsMapType = WordInterface[][];

export class Lyrics {
  private _model: WordInterface[][] = [];

  constructor() {
    // empty
  }

  get model(): LyricsMapType {
    return this._model;
  }

  public static buildFromText(text: string): Lyrics {
    const lyrics: Lyrics = new Lyrics();

    // 1. Rimuovere gli spazi in eccesso tra le parole
    const trimmedLines: string[] = text
      .split("\n")
      .map((line) => line.trim()) // Rimuove gli spazi iniziali e finali
      .map((line) => line.replace(/\s+/g, " ")); // Rimuove gli spazi multipli tra le parole

    for (let i = 0; i < trimmedLines.length; i++) {
      lyrics.addNewRow(i);
      const line = trimmedLines[i];
      const splitLine: string[] = line.split(" ").filter((k) => !isEmpty(k));
      for (let j = 0; j < splitLine.length; j++) {
        const wordText: string = splitLine[j];
        lyrics.appendWord(i, Lyrics.parseWord(wordText));
      }
    }

    return lyrics;
  }

  public static parseWord(input: string): WordInterface {
    let text: string | undefined = "INVALID";
    let chord: string | undefined;
    let offset: number | undefined;
    let margin: number | undefined;

    const float = "[+-]?(?:[0-9]*[.])?[0-9]*";

    const match = input.match(`(?:{([^{},]*)?,(${float})?,(${float})?})?(.+)`);

    if (match) {
      if (match[1]) {
        chord = match[1];
      }
      if (match[2]) {
        offset = parseFloat(match[2]);
      }
      if (match[3]) {
        margin = parseFloat(match[3]);
      }
      text = match[4] === "{empty}" ? " " : match[4];
    }

    return {
      text,
      chord,
      offset,
      margin,
    };
  }

  public addNewRow(idx: number): LyricsMapType {
    this.insertRowAtIndex(idx, []);
    return this.model;
  }

  public deleteRow(idx: number): LyricsMapType {
    this.removeRowAtIndex(idx);
    return this.model;
  }

  public addWord(rowIdx: number, idx: number, text: string): LyricsMapType {
    const row: WordInterface[] | undefined = this.model[rowIdx];
    if (row === undefined) {
      return this.model;
    }

    // A B C
    // 0 1 2
    //
    // A D B C
    // 0 1 2 3

    const word: WordInterface = {
      text: text,
      chord: undefined,
      offset: 0,
    };

    row.splice(idx, 0, word);

    return this.model;
  }

  public appendWord(idx: number, word: WordInterface): LyricsMapType {
    const row: WordInterface[] | undefined = this.model[idx];

    if (row === undefined) return this.model;

    row.push(word);

    return this.model;
  }

  public deleteWord(rowIdx: number, wordIdx: number): LyricsMapType {
    const row: WordInterface[] | undefined = this.model[rowIdx];

    if (row === undefined) {
      return this.model;
    }

    row.splice(wordIdx, 1);

    if (row.length === 0) {
      this.removeRowAtIndex(rowIdx);
    }

    return this.model;
  }

  public updateWord(
    rowIdx: number,
    wordIdx: number,
    word: WordInterface
  ): LyricsMapType {
    const row: WordInterface[] | undefined = this.model[rowIdx];

    if (row === undefined) return this.model;

    row[wordIdx] = { ...word };

    return this.model;
  }

  private insertRowAtIndex(idx: number, value: WordInterface[]): void {
    this.model.splice(idx, 0, value);
  }

  private removeRowAtIndex(idx: number): void {
    this.model.splice(idx, 1);
  }

  public toString(): string {
    let result = "";
    for (let i = 0; i < this.model.length; i++) {
      const row: WordInterface[] = this.model[i]!;
      for (let j = 0; j < row.length; j++) {
        const word: WordInterface = row[j];
        if (word.chord || word.offset || word.margin) {
          result += "{";
          if (word.chord !== undefined) {
            result += word.chord;
          }
          result += ",";
          if (word.offset !== undefined && word.offset != 0) {
            result += word.offset;
          }
          result += ",";
          if (word.margin !== undefined && word.margin != 0) {
            result += round(word.margin, 2).toString(10);
          }
          result += "}";
        }

        result += isEmpty(word.text) ? "{empty}" : word.text;
        if (j < row.length - 1) {
          result += " ";
        }
      }
      if (i < this.model.length - 1) {
        result += "\n";
      }
    }
    return result;
  }

  public getChords(): string[] {
    const words: WordInterface[] = Array.from(this.model.values()).reduce(
      (prev, curr) => prev.concat(curr),
      []
    );
    const chords: string[] = words
      .filter((word: WordInterface) => word.chord !== undefined)
      .map((word: WordInterface) => word.chord!);
    return Array.from(new Set(chords));
  }
}
