<template>
  <div
    class="row-number"
    @mouseenter="isMouseEnter"
    @mouseleave="isMouseLeave"
    :style="{ cursor: isOverNumber ? 'pointer' : '' }"
  >
    <!-- hidden is much faster than if -->
    <div :hidden="isOverNumber" class="number fs-6 fw-light">
      {{ idx + 1 }}.
    </div>
    <div :hidden="!isOverNumber" class="delete">
      <clickable-icon
        icon="trash.svg"
        :style="{
          padding: 0,
        }"
        v-on:onClick="onDeleteRow"
      ></clickable-icon>
    </div>
  </div>
</template>

<script setup lang="ts">
import { inject, ref, Ref } from "vue";
import ClickableIcon from "@/components/clickable-icon.component.vue";
import { EditorMenuService } from "@/services/editor-menu.service";

const editorMenuService: EditorMenuService = inject("editorMenuService")!;

const props: { idx: number } = defineProps<{ idx: number }>();
const emitter = defineEmits<{
  (e: "onDeleteRow", idx: number): void;
}>();
const isOverNumber: Ref<boolean> = ref(false);

function onDeleteRow(): void {
  emitter("onDeleteRow", props.idx);
}

function isMouseEnter(): void {
  if (!editorMenuService.isSelected()) {
    isOverNumber.value = true;
  }
}

function isMouseLeave(): void {
  if (!editorMenuService.isSelected()) {
    isOverNumber.value = false;
  }
}
</script>

<style scoped lang="scss">
.row-number {
  display: flex;
  justify-content: end;
  height: 100%;
  width: 2rem;
  min-width: 2rem;
  color: var(--bs-gray-600);

  .delete {
    display: flex;
    justify-content: end;
    width: 100%;
    cursor: pointer;
  }
}
</style>
