<template>
  <div class="d-flex justify-content-center align-items-center mb-1">
    <div :style="{ width: '48px' }">
      <clickable-icon
        icon="decrease_font.png"
        v-on:click="fontSize -= 0.1"
      ></clickable-icon>
    </div>
    <div class="mx-3" :style="{ 'font-size': `${fontSize}rem` }">
      {{ $t("app.print_preview.font_size") }}
    </div>
    <div :style="{ width: '48px' }">
      <clickable-icon
        icon="increase_font.png"
        v-on:click="fontSize += 0.1"
      ></clickable-icon>
    </div>
  </div>
  <div
    ref="printPreviewEl"
    class="container p-0"
    :style="{ 'font-size': `${fontSize}rem`, display: `none` }"
  >
    <div class="row">
      <div class="col">
        <div class="text-start">
          <div class="h1">{{ song.title }}</div>
          <div class="h3">{{ song.author }}</div>
        </div>
      </div>
    </div>
    <div class="row">
      <div class="col">
        <div class="text-start mb-2" :style="{ fontSize: `1rem` }">
          {{ song.lyrics.getChords().join(" ") }}
        </div>
      </div>
    </div>
    <div class="row pb-2">
      <div class="col">
        <div class="border-bottom"></div>
      </div>
    </div>
    <div
      v-for="(row, idx1) in song.lyrics?.model.values()"
      :key="idx1"
      class="row"
    >
      <div class="col">
        <div class="d-flex align-items-end">
          <div v-if="row.length === 0">&nbsp;</div>
          <div
            v-else
            class="d-flex flex-column me-2"
            v-for="(word, idx2) in row"
            :key="idx1 + idx2 + word.text"
          >
            <div
              v-if="word.chord"
              class="chord position-relative"
              :style="{ height: `${fontSize}rem` }"
            >
              <div
                class="chord-text position-absolute"
                :style="{ left: `${(word.offset ?? 0) * fontSize}px` }"
              >
                <b>{{ word.chord }}</b>
              </div>
            </div>
            <div
              class="word"
              :style="{
                margin: `0 ${word.margin}rem 0 ${
                  !isFirst(idx2) ? word.margin + 'rem' : '0'
                }`,
              }"
            >
              <div v-if="isEmpty(word.text)">&nbsp;</div>
              <div v-else>{{ word.text }}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="row mt-4">
      <div class="col">
        <div
          class="credits text-start text-muted"
          :style="{ 'font-size': '0.8rem' }"
        >
          <em>{{ $t("app.print_preview.credits") }}</em>
        </div>
      </div>
    </div>
  </div>
  <div class="d-flex justify-content-center">
    <button
      type="button"
      class="print-button btn btn-primary"
      @click="printPreview()"
    >
      <i class="bi bi-printer"></i>
    </button>
    <div :style="{ width: '2rem' }">
      <clickable-icon
        icon="filetype-pdf.svg"
        v-on:onClick="saveAsPDF"
      ></clickable-icon>
    </div>
  </div>
</template>

<script setup lang="ts">
import { onMounted, ref, Ref } from "vue";
import { Song } from "@/interfaces/interfaces";
import ClickableIcon from "@/components/clickable-icon.component.vue";
import { isEmpty } from "@/utils/utils";
import jsPDF from "jspdf";

const props: { song: Song } = defineProps<{ song: Song }>();

const printPreviewEl: Ref<HTMLElement | undefined> = ref();
const fontSize: Ref<number> = ref(1);

function isFirst(idx2: number) {
  return idx2 === 0;
}

onMounted(() => {
  // empty
});

function saveAsPDF(): void {
  const printWindow: Window | null = createPrintWindow();
  const doc = new jsPDF({
    orientation: "p",
    unit: "px",
    format: "a4",
  });

  if (printWindow == null) return;

  doc.html(printWindow.document.documentElement, {
    callback: (doc) => {
      doc.save(`${props.song.author}-${props.song.title}`);
      printWindow.close();
    },
    margin: [20, 20, 20, 20], // mm
  });
}

function printPreview(): void {
  const printWindow: Window | null = createPrintWindow();

  if (printWindow == null) return;

  printWindow.print();
}

function createPrintWindow(): Window | null {
  const printWindow: Window | null = window.open(
    "",
    "_blank",
    "width=800,height=600"
  )!;
  const content = printPreviewEl.value!.innerHTML;
  const styles = document.querySelectorAll('style, link[rel="stylesheet"]');
  styles.forEach((style) => {
    printWindow.document.write(style.outerHTML);
  });
  printWindow.document.write(content);
  printWindow.document.write(`
    <style>
      body {
        font-size: ${fontSize.value}rem;
      }
    </style>`);
  printWindow.document.close();
  return printWindow;
}
</script>

<style scoped lang="scss">
.chord {
  // empty
}
.chord-text {
  width: fit-content;
}
.print-button {
  width: 8rem;
}
</style>
