<template>
  <clickable-icon
    :icon="appStateService.isDark.value ? 'moon.svg' : 'sun.svg'"
    v-on:onClick="toggleDarkMode"
    :style="{
      width: 32,
      height: 32,
    }"
  ></clickable-icon>
</template>

<script setup lang="ts">
import ClickableIcon from "@/components/clickable-icon.component.vue";
import { inject } from "vue";
import { AppStateService } from "@/services/appStateService";

const appStateService: AppStateService = inject("appStateService")!;

function toggleDarkMode(): void {
  appStateService.toggleDarkMode();
}
</script>

<style scoped lang="scss"></style>
