<template>
  <div class="chord-container">
    <div
      ref="chordWrapperEl"
      class="chord-wrapper"
      :style="{ left: `${offset}px` }"
    >
      <div
        ref="chordEl"
        class="chord"
        @mouseenter="onMouseEnter"
        @mouseleave="onMouseLeave"
      >
        <b v-if="showText">{{ chord }}</b>
      </div>
      <div
        ref="leftEl"
        class="left-arrow"
        :style="{
          top: 0,
          left: `${chordRelativeXPos - arrowWidth}px`,
        }"
        @mouseenter="onMouseEnter"
        @mouseleave="onMouseLeave"
        :hidden="!showArrows"
      >
        <clickable-icon
          icon="arrow-left.svg"
          :onmousedown="onClickDownLeft"
          :onmouseup="onClickUpLeft"
        ></clickable-icon>
      </div>
      <div
        ref="rightEl"
        class="right-arrow"
        :style="{
          top: 0,
          left: `${chordRelativeXPos + chordWidth}px`,
        }"
        @mouseenter="onMouseEnter"
        @mouseleave="onMouseLeave"
        :hidden="!showArrows"
      >
        <clickable-icon
          icon="arrow-right.svg"
          :onmousedown="onClickDownRight"
          :onmouseup="onClickUpRight"
        ></clickable-icon>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { inject, onBeforeUnmount, onMounted, ref, Ref, watch } from "vue";
import ClickableIcon from "@/components/clickable-icon.component.vue";
import { EditorMenuService } from "@/services/editor-menu.service";

interface Props {
  chord?: string;
  chordOffset?: number;
  showText?: boolean;
}

const editorMenuService: EditorMenuService = inject("editorMenuService")!;

const props: Props = withDefaults(defineProps<Props>(), {
  showText: true,
});

const emitter = defineEmits<{
  (e: "updateOffset", offset: number): void;
}>();

const offset: Ref<number> = ref(0);

watch(offset, (value) => {
  emitter("updateOffset", value);
});

watch(
  () => props.chordOffset,
  (value) => {
    offset.value = value ?? 0;
  },
  { immediate: true }
);

const DEFAULT_OFFSET_INC = 5;
let offsetInc = DEFAULT_OFFSET_INC;

const chordWrapperEl: Ref<HTMLElement | null> = ref(null);
const leftEl: Ref<HTMLElement | null> = ref(null);
const rightEl: Ref<HTMLElement | null> = ref(null);
const chordEl: Ref<HTMLElement | null> = ref(null);
const showArrows: Ref<boolean> = ref(false);
const chordRelativeXPos: Ref<number> = ref(0);
const containerWidth: Ref<number> = ref(0);
const chordWidth: Ref<number> = ref(0);
const arrowWidth: Ref<number> = ref(0);
const shiftLeftPressed: Ref<boolean> = ref(false);

watch(shiftLeftPressed, (value) => {
  offsetInc = value ? 1 : DEFAULT_OFFSET_INC;
});

const leftArrowResizeObserver:ResizeObserver = new ResizeObserver((div) => {
  arrowWidth.value = div[0].target.getBoundingClientRect().width;
});

const chordResizeObserver:ResizeObserver = new ResizeObserver((div) => {
  if (chordWrapperEl.value != null) {
    const chordEl: Element = div[0].target;
    chordWidth.value = chordEl.getBoundingClientRect().width;
    chordRelativeXPos.value =
      chordEl.getBoundingClientRect().left -
      chordWrapperEl.value.getBoundingClientRect().left;
  }
});

const handleClickOutside = (event: any) => {
  if (
    (leftEl.value && !leftEl.value.contains(event.target)) ||
    (rightEl.value && !rightEl.value.contains(event.target))
  ) {
    if (incLeftInterval != null) {
      clearTimeout(incLeftInterval);
    }
    if (incRightInterval != null) {
      clearTimeout(incRightInterval);
    }
  }
};

const handleShiftLeftDownEvent = (e: KeyboardEvent): void => {
  if (e.code === "ShiftLeft") {
    shiftLeftPressed.value = true;
  }
};
const handleShiftLeftUpEvent = (e: KeyboardEvent): void => {
  if (e.code === "ShiftLeft") {
    shiftLeftPressed.value = false;
  }
};

onMounted(() => {
  document.addEventListener("keydown", handleShiftLeftDownEvent);
  document.addEventListener("keyup", handleShiftLeftUpEvent);
  document.addEventListener("click", handleClickOutside);
  document.addEventListener("dragend", handleClickOutside);
  if (leftEl.value != null) {
    leftArrowResizeObserver.observe(leftEl.value);
  }
  if (chordEl.value != null) {
    chordResizeObserver.observe(chordEl.value);
  }
  if (chordWrapperEl.value != null) {
    containerWidth.value = chordWrapperEl.value.getBoundingClientRect().width;
  }
});

onBeforeUnmount(() => {
  document.removeEventListener("keydown", handleShiftLeftDownEvent);
  document.removeEventListener("keyup", handleShiftLeftUpEvent);
  document.removeEventListener("click", handleClickOutside);
  document.removeEventListener("dragend", handleClickOutside);
  leftArrowResizeObserver.disconnect();
  chordResizeObserver.disconnect();
});

function incLeft(): void {
  offset.value -= offsetInc;
}

function incRight(): void {
  offset.value += offsetInc;
}

const intervalDelay = 100;
let incRightInterval: number | undefined = undefined;
let incLeftInterval: number | undefined = undefined;

function onClickDownLeft(): void {
  incLeft();
  incLeftInterval = setInterval(() => incLeft(), intervalDelay);
}

function onClickUpLeft(): void {
  if (incLeftInterval != null) {
    clearInterval(incLeftInterval);
  }
}

function onClickDownRight(): void {
  incRight();
  incRightInterval = setInterval(() => incRight(), intervalDelay);
}

function onClickUpRight(): void {
  if (incRightInterval != null) {
    clearInterval(incRightInterval);
  }
}

function onMouseEnter(): void {
  if (!editorMenuService.isSelected()) {
    showArrows.value = true;
  }
}

function onMouseLeave(): void {
  if (!editorMenuService.isSelected()) {
    showArrows.value = false;
  }
}
</script>

<style scoped lang="scss">
.chord-container {
  position: absolute;
  .chord-wrapper {
    display: flex;
    justify-content: center;
    position: relative;
  }
  .chord {
    cursor: pointer;
    width: fit-content;
  }
  .left-arrow {
    position: absolute;
    height: 100%;
    z-index: 1;
  }
  .right-arrow {
    position: absolute;
    height: 100%;
    z-index: 1;
  }
}
</style>
