import { Song } from "@/interfaces/interfaces";
import { reactive, Reactive } from "vue";
import { Lyrics } from "@/models/lyrics.model";

export class AppStateService {
  public song: Reactive<Song> = reactive<Song>({
    author: undefined,
    title: undefined,
    lyrics: new Lyrics(),
  });

  public isDark: Reactive<{ value: boolean }> = reactive({ value: false });

  constructor() {
    // empty
  }

  public setDarkMode(value: boolean): void {
    this.isDark.value = value;
    const _value: string = this.isDark.value ? "dark" : "light";
    document
      .getElementsByTagName("html")[0]!
      .setAttribute("data-bs-theme", _value);
  }

  public toggleDarkMode(): void {
    this.isDark.value = !this.isDark.value;
    sessionStorage.setItem("darkMode", this.isDark.value.toString());
    const _value: string = this.isDark.value ? "dark" : "light";
    document
      .getElementsByTagName("html")[0]
      .setAttribute("data-bs-theme", _value);
  }

  public updateAuthor(author: string): void {
    this.song.author = author;
  }

  public updateTitle(title: string): void {
    this.song.title = title;
  }
}
