<template>
  <div class="new-word-container">
    <div
      ref="fakeEl"
      class="position-absolute"
      :style="{ visibility: 'hidden' }"
    >
      {{ form.text }}
    </div>
    <template v-if="showForm">
      <input
        ref="formEl"
        class="form"
        type="text"
        v-model="form.text"
        @keyup.enter="handleEnter"
        @keydown.space="(e: KeyboardEvent) => e.preventDefault()"
        @keyup.esc="handleEsc"
        :style="{ width: `${inputWidth}px` }"
      />
    </template>
    <template v-else>
      <div
        class="area"
        @mouseenter="showCursor = true"
        @mouseleave="showCursor = false"
        @click="onClickNewWord"
        :style="{
          cursor: showPointer ? 'pointer' : '',
        }"
      ></div>
      <div v-if="showCursor" class="position-relative">
        <div class="new-word-icon position-absolute">
          <i class="bi bi-geo-alt"></i>
        </div>
      </div>
    </template>
  </div>
</template>

<script setup lang="ts">
import {inject, onBeforeUnmount, onMounted, reactive, Reactive, ref, Ref, watch} from "vue";
import { isEmpty } from "@/utils/utils";
import {
  EditorMenuAction,
  EditorMenuService,
} from "@/services/editor-menu.service";

const editorMenuService: EditorMenuService = inject("editorMenuService")!;

const emitter = defineEmits<{
  (e: "onAddNewWord", text: string | undefined): void;
}>();

const fakeEl: Ref<HTMLElement | null> = ref(null);
const formEl: Ref<HTMLElement | null> = ref(null);
const showForm: Ref<boolean> = ref(false);
const showCursor: Ref<boolean> = ref(false);
const showPointer: Ref<boolean> = ref(true);
const addEmptyWord: Reactive<EditorMenuAction> =
  editorMenuService.actions.addEmptyWord;
let emit = false;
const inputWidth: Ref<number> = ref(1);
const form: Reactive<{ text: string | undefined }> = reactive({
  text: undefined,
});

let avoidFirstHandle = true;

watch(formEl, (value) => {
  if (value != null) {
    value.focus();
  }
});
watch(showForm, (value) => {
  if (value) {
    avoidFirstHandle = true;
    emit = true;
  } else {
    if (emit && !isEmpty(form.text)) {
      emitter("onAddNewWord", form.text);
    }
    form.text = undefined;
  }
});
watch(
  addEmptyWord,
  (value) => {
    showPointer.value = !value.selected;
  },
  { immediate: true }
);
const resizeObserver: ResizeObserver = new ResizeObserver((div) => {
  const width: number = div[0].target.getBoundingClientRect().width;
  inputWidth.value = width === 0 ? 1 : width;
});

const handleClickOutside = (event: any) => {
  if (
    !avoidFirstHandle &&
    formEl.value &&
    !formEl.value.contains(event.target)
  ) {
    showForm.value = false;
  }
  avoidFirstHandle = false;
};

onMounted(() => {
  resizeObserver.observe(fakeEl.value!);
  document.addEventListener("click", handleClickOutside, {});
});

onBeforeUnmount(() => {
  document.removeEventListener("click", handleClickOutside);
  resizeObserver.disconnect();
})

function onClickNewWord(): void {
  if (addEmptyWord.selected) {
    emitter("onAddNewWord", "");
    editorMenuService.toggleAddEmptyWord();
  } else {
    showCursor.value = false;
    showForm.value = true;
  }
}

function handleEnter(): void {
  showForm.value = false;
}

function handleEsc(): void {
  showForm.value = false;
  emit = false;
}
</script>

<style scoped lang="scss">
.new-word-container {
  position: relative;
  height: 1.5rem;
  align-self: end;
  .area {
    position: absolute;
    width: 0.4rem;
    height: 100%;
    //border: 1px solid blue;
    left: -0.2rem;
    z-index: 1;
  }
  .new-word-icon {
    top: -0.6rem;
    left: -0.5rem;
  }
  .form {
    width: 3rem;
  }
}

input[type="text"] {
  all: unset;
  text-align: start;
}

input[type="text"]:focus-visible {
  outline: none;
}
</style>
