<template>
  <div id="editor" class="editor container-fluid">
    <div class="row">
      <div class="col p-0 border rounded">
        <div ref="scrollEl" class="scroll-y" :style="{height: `${scrollElHeight}px`}">
          <div ref="scrollContentEl">
            <div class="row">
              <div class="col p-0 position-relative">
                <new-row
                    v-if="!editorMenuService.isSelected()"
                    v-on:click="onAddNewRow(0)"
                    :row-width="colWidth"
                ></new-row>
              </div>
            </div>
            <div
                v-for="(value, idx1) in lyrics.model"
                :key="idx1"
                class="row position-relative"
                ref="rowEls"
                :style="{ 'user-select': 'none' }"
            >
              <div class="col p-0">
                <div class="d-flex align-items-center">
                  <row-number
                      :idx="idx1"
                      v-on:onDeleteRow="(rowId: number) => onDeleteRow(rowId)"
                  ></row-number>
                  <div class="divider">&nbsp;</div>
                  <div class="d-flex flex-wrap align-items-end">
                    <new-word
                        v-if="
                      editorMenuService.isAddEmptyWordSelected() ||
                      !editorMenuService.isSelected()
                    "
                        v-on:onAddNewWord="(_text: string) => onAddNewWord(idx1, 0, _text)"
                    ></new-word>
                    <template
                        v-for="(word, idx2) in value"
                        :key="idx1 + idx2 + word.text"
                    >
                      <word
                          :word="word"
                          v-on:onUpdate="(_word: WordInterface) => updateWord(idx1, idx2, _word)"
                          v-on:onDelete="onDeleteWord(idx1, idx2)"
                      ></word>
                      <new-word
                          v-if="
                        editorMenuService.isAddEmptyWordSelected() ||
                        !editorMenuService.isSelected()
                      "
                          v-on:onAddNewWord="(_text: string) => onAddNewWord(idx1, idx2 + 1, _text)"
                      ></new-word>
                    </template>
                  </div>
                </div>
              </div>
              <new-row
                  v-if="!editorMenuService.isSelected()"
                  v-on:click="onAddNewRow(idx1 + 1)"
                  :row-height="rowHeights[idx1]"
                  :row-width="colWidth"
              ></new-row>
            </div>
          </div>
        </div>
      </div>
      <div class="col-auto p-0">
        <editor-menu></editor-menu>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import {inject, onBeforeUnmount, onMounted, Reactive, Ref, ref} from "vue";
import Word from "@/components/word.component.vue";
import NewRow from "@/components/new-row.component.vue";
import NewWord from "@/components/new-word.component.vue";
import {Lyrics} from "@/models/lyrics.model";
import {WordInterface} from "@/interfaces/interfaces";
import RowNumber from "@/components/row-number.component.vue";
import EditorMenu from "@/components/editor-menu.component.vue";
import {EditorMenuService} from "@/services/editor-menu.service";

const editorMenuService: EditorMenuService = inject("editorMenuService")!;

const props: Reactive<{ lyrics: Lyrics }> = defineProps<{ lyrics: Lyrics }>();

const emitter = defineEmits<{
  (e: "onEdit"): void;
}>();

const scrollContentEl: Ref<HTMLElement | undefined> = ref();
const rowEls: Ref<HTMLElement[]> = ref([]);
const scrollEl: Ref<HTMLElement | undefined> = ref();
const scrollElHeight: Ref<number | undefined> = ref();
const colWidth: Ref<number | undefined> = ref();
const rowHeights: Ref<number[]> = ref([]);

const scrollContentElObs: ResizeObserver = new ResizeObserver((_) => {
  rowHeights.value = [];
  for (let i = 0; i < rowEls.value.length; i++) {
    const rowEl: HTMLElement = rowEls.value[i];
    rowHeights.value[i] = rowEl.clientHeight;
  }
});

onMounted(() => {
  computeScrollEl();
  window.addEventListener("resize", computeScrollEl);
  if (scrollContentEl.value !== undefined) {
    scrollContentElObs.observe(scrollContentEl.value);
  }
  document.addEventListener("keyup", handleEsc);
});

onBeforeUnmount(() => {
  document.removeEventListener("keyup", handleEsc);
  window.removeEventListener("resize", computeScrollEl);
  scrollContentElObs.disconnect();
});

function computeScrollEl(): void {
  const el: HTMLElement = document.getElementById("lyrics-input")!;
  scrollElHeight.value = el.getBoundingClientRect().height;
  computeColWidth(el);
}

function computeColWidth(el: HTMLElement): void {
  const a = parseFloat(
      getComputedStyle(el).width.replace("px", "")
  );
  const b =
      parseFloat(
          getComputedStyle(el).paddingRight.replace("px", "")
      ) * 2;
  const c = parseFloat(
      getComputedStyle(el).borderWidth.replace("px", "")
  );
  colWidth.value = a - b - c;
}

function updateWord(
    rowIdx: number,
    wordIdx: number,
    word: WordInterface
): void {
  props.lyrics.updateWord(rowIdx, wordIdx, word);
  emitter("onEdit");
}

function onAddNewRow(idx: number): void {
  props.lyrics.addNewRow(idx);
  emitter("onEdit");
}

function onAddNewWord(rowId: number, wordIdx: number, text: string): void {
  props.lyrics.addWord(rowId, wordIdx, text);
  emitter("onEdit");
}

function onDeleteRow(key: number): void {
  props.lyrics.deleteRow(key);
  emitter("onEdit");
}

function onDeleteWord(rowIdx: number, wordIdx: number): void {
  props.lyrics.deleteWord(rowIdx, wordIdx);
  emitter("onEdit");
}

function handleEsc(e: KeyboardEvent): void {
  if (e.code === "Escape") {
    editorMenuService.switchAllOff();
  }
}
</script>

<style scoped lang="scss">
@import "@/variables.scss";

.editor {
  .divider {
    border-right: 1px solid var(--bs-gray-300);
    margin-left: 0.25rem;
    margin-right: 0.25rem;
  }
}

.eraser-cursor {
  cursor: $eraser-cursor !important;
}

.edit-word-text-cursor {
  cursor: $edit-word-text-cursor !important;
}

.add-empty-word-cursor {
  cursor: $add-empty-word-cursor !important;
}

.spacing-word-cursor {
  cursor: $spacing-word-cursor !important;
}

</style>
