import { reactive, Reactive } from "vue";

export interface ScrollServiceState {
  scrollDirection: string | undefined;
  event: WheelEvent | undefined;
}

export class ScrollService {
  public state: Reactive<ScrollServiceState> = reactive({
    scrollDirection: undefined,
    event: undefined,
  });

  constructor() {
    document.addEventListener("wheel", (ev) => this.onScroll(ev, this.state), {
      passive: false,
    });
  }

  private onScroll(ev: WheelEvent, state: ScrollServiceState): void {
    state.event = ev;
    state.scrollDirection = ev.deltaY < 0 ? "up" : "down";
  }
}
