<template>
  <div class="menu-container ms-2">
    <div class="menu p-2 border rounded">
      <div class="menu-item">
        <clickable-icon
          icon="eraser.svg"
          :selected="actions.eraseWord.selected"
          v-on:onClick="onEraseWord"
          :tooltip="$t('app.editor_menu.eraser_tooltip')"
        ></clickable-icon>
      </div>
      <div class="menu-item">
        <clickable-icon
          icon="pencil.svg"
          :selected="actions.editWordText.selected"
          :tooltip="$t('app.editor_menu.edit_word_text_tooltip')"
          v-on:onClick="onEditWordText"
        ></clickable-icon>
      </div>
      <div class="menu-item">
        <clickable-icon
          icon="asterisk.svg"
          :selected="actions.addEmptyWord.selected"
          :tooltip="$t('app.editor_menu.add_empty_word_tooltip')"
          v-on:onClick="onAddEmptyWord"
        ></clickable-icon>
      </div>
      <div class="menu-item">
        <clickable-icon
          icon="wise-arrows.svg"
          :selected="actions.spacingWord.selected"
          :tooltip="$t('app.editor_menu.spacing_word_tooltip')"
          v-on:onClick="onSpacingWord"
        ></clickable-icon>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import ClickableIcon from "@/components/clickable-icon.component.vue";
import { inject, Reactive } from "vue";
import {
  EditorMenuActions,
  EditorMenuService,
} from "@/services/editor-menu.service";

const editorMenuService: EditorMenuService = inject("editorMenuService")!;
const actions: Reactive<EditorMenuActions> = editorMenuService.actions;

function onEraseWord(): void {
  editorMenuService.toggleEraseWord();
}

function onEditWordText(): void {
  editorMenuService.toggleEditWordText();
}

function onAddEmptyWord(): void {
  editorMenuService.toggleAddEmptyWord();
}

function onSpacingWord(): void {
  editorMenuService.toggleSpacingWord();
}
</script>

<style scoped lang="scss">
.menu-container {
  height: 100%;

  .menu {
    // empty
    .menu-item {
      // empty
    }
  }
}
</style>
