<template>
  <div
    ref="containerEl"
    class="chord-form-container"
    :style="{ left: `${offsetLeft}px` }"
  >
    <input
      ref="input"
      type="text"
      v-model="state.chord"
      @input="validateInput"
      @keyup.enter="handleEnter"
      @keyup.esc="handleEsc"
      @keyup.space="(e: KeyboardEvent) => e.preventDefault()"
    />
  </div>
</template>

<script setup lang="ts">
import {
  inject,
  onBeforeUnmount,
  onMounted,
  onUnmounted,
  reactive,
  ref,
  Ref,
} from "vue";
import { helpers } from "@vuelidate/validators";
import useVuelidate from "@vuelidate/core";
import { ChordFormService } from "@/services/chord-form.service";
import { isEmpty } from "@/utils/utils";

interface ChordFormProps {
  chord: string | undefined;
  textWidth: number;
}

const chordFormService: ChordFormService = inject("chordFormService")!;

const props: ChordFormProps = defineProps<ChordFormProps>();

const emitter = defineEmits<{
  (e: "onClose", chord: string | undefined): void;
}>();

const containerEl: Ref<HTMLElement | null> = ref(null);
const input: Ref<HTMLElement | null> = ref(null);
const offsetLeft: Ref<number> = ref(0);

const allowedCharsRegex = /^[a-zA-Z0-9#/]*$/;
const notAllowedCharsRegex = /[^a-zA-Z0-9#/]/g;

const state: {
  chord: string | undefined;
} = reactive<{ chord: string | undefined }>({
  chord: props.chord,
});

const rules = {
  chord: helpers.withMessage("", helpers.regex(allowedCharsRegex)),
};

const v$ = useVuelidate(rules, state);

let emit = true;

const handleClickOutside = (event: any) => {
  if (containerEl.value && !containerEl.value.contains(event.target)) {
    chordFormService.hideAll();
  }
};

onMounted(() => {
  document.addEventListener("click", handleClickOutside);
  if (input.value != null) {
    input.value.focus();
  }
  if (containerEl.value != null) {
    offsetLeft.value =
      (props.textWidth - containerEl.value.getBoundingClientRect().width) / 2;
  }
});

onBeforeUnmount(() => {
  document.removeEventListener("click", handleClickOutside);
});

onUnmounted(() => {
  if (state.chord !== props.chord && emit) {
    state.chord = isEmpty(state.chord) ? undefined : state.chord;
    emitter("onClose", state.chord);
  }
});

// prevent not valid chars
function validateInput(): void {
  if (state.chord != null && !allowedCharsRegex.test(state.chord)) {
    state.chord = state.chord.replace(notAllowedCharsRegex, "");
  }
}

function handleEnter(): void {
  chordFormService.hideAll();
}

function handleEsc(): void {
  chordFormService.hideAll();
  emit = false;
}
</script>

<style scoped lang="scss">
.chord-form-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: -1.5rem;
  z-index: 2;
}
input[type="text"] {
  all: unset;
  width: 2rem;
  height: 1.8rem;
  //border: 1px solid var(--bs-gray-200);
  border-radius: 0.2rem;
  background-color: white;
  box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.05);
  text-align: start;
  padding: 0 0.25rem;
  //caret-color: transparent;
}
</style>
