<template>
  <div
    class="new-row-container"
    @mouseenter="isMouseOver = true"
    @mouseleave="isMouseOver = false"
    @click="onClick"
    :class="{ 'new-row-container-over': isMouseOver }"
    :style="{ top: `${rowHeight! - 6}px`, width: `${rowWidth}px` }"
  >
    <div class="icon" v-if="isMouseOver">
      <i class="bi bi-pencil"></i>
    </div>
    <div class="line" v-if="showLine && isMouseOver"></div>
  </div>
</template>

<script setup lang="ts">
import { Ref, ref } from "vue";

const isMouseOver: Ref<boolean> = ref(false);

const props: { rowHeight: number; rowWidth: number; showLine: boolean } =
  withDefaults(
    defineProps<{
      rowHeight?: number;
      rowWidth?: number;
      showLine?: boolean;
    }>(),
    { rowHeight: 0, rowWidth: 0, showLine: true }
  );

const emitter = defineEmits<{
  (e: "click"): void;
}>();

function onClick(): void {
  emitter("click");
}
</script>

<style scoped lang="scss">
.new-row-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: absolute;
  height: 0.8rem;
  z-index: 1;
  //border: 1px solid blue;

  .icon {
    position: absolute;
    top: -1rem;
    z-index: 1;
  }
  .line {
    width: 100%;
    border-bottom: 1px solid var(--bs-gray-300);
  }
}

.new-row-container-over {
  cursor: pointer;
}
</style>
