<template>
  <div class="home d-flex flex-column">
    <!--    HEADER     -->
    <div class="app-header">
      <div>
        <div class="contacts fw-light text-muted">
          {{$t("app.home.contact")}}:{{appContact}}
        </div>
        <div class="d-flex align-items-end">
          <h2 class="m-0 me-1">{{ $t("app.home.name") }}</h2>
          <div class="version fw-light text-muted">v.{{appVersion}}</div>
        </div>
      </div>
      <div class="ms-auto">
        <dark-mode></dark-mode>
      </div>
    </div>
    <div class="divider"></div>
    <div class="song-info">
      <!--    TITLE    -->
      <div class="d-flex align-items-center w-25 mb-2">
        <label class="label m-0 me-2 form-label" for="song-author">{{
          $t("song.author")
        }}</label>
        <input
          class="form-control"
          type="text"
          id="song-author"
          v-model="song.author"
          aria-describedby="song-author"
        />
      </div>
      <!--    AUTHOR    -->
      <div class="d-flex align-items-center w-25 mb-2">
        <label class="label m-0 me-2 form-label" for="song-title">{{
          $t("song.title")
        }}</label>
        <input
          class="form-control"
          type="text"
          id="song-title"
          v-model="song.title"
          aria-describedby="song-title"
        />
      </div>
      <div class="divider"></div>
    </div>
    <div class="divider"></div>
    <div class="d-flex flex-grow-1">
      <!--    LYRICS    -->
      <div
        id="lyrics"
        class="lyrics d-flex flex-column"
        :style="{ width: '50%' }"
      >
        <label for="lyrics" class="form-label">{{
          $t("app.home.lyrics")
        }}</label>
        <textarea
          v-model="text"
          class="lyrics-input form-control"
          id="lyrics-input"
          :style="{
            width: '100%',
            height: '100%',
            resize: 'none',
          }"
        ></textarea>
      </div>
      <div class="d-flex gap-2 flex-column justify-content-center mx-2">
        <button
            :disabled="!enableButtons"
            type="button"
            class="btn btn-primary mx-2"
            @click="processText()"
        >
          <i class="bi bi-arrow-right"></i>
        </button>
        <button
            :disabled="!enableButtons"
            type="button"
            class="btn btn-primary mx-2"
            @click="toText()"
        >
          <i class="bi bi-arrow-left"></i>
        </button>
      </div>
      <!--  EDITOR  -->
      <div
        id="lyrics-editor"
        class="d-flex flex-column"
        :style="{ width: '50%' }"
      >
        <label for="lyrics" class="form-label">{{
          $t("app.home.editor")
        }}</label>
          <editor :lyrics="song.lyrics" v-on:onEdit="doEnableButtons"></editor>
      </div>
    </div>
    <div class="divider"></div>
    <!--  PRINT PREVIEW  -->
    <div>
      <print-preview :song="song"></print-preview>
    </div>
  </div>
</template>

<script setup lang="ts">
import { inject, onMounted, Reactive, Ref, ref, watch } from "vue";
import Editor from "@/components/editor.component.vue";
import { AppStateService } from "@/services/appStateService";
import { Lyrics } from "@/models/lyrics.model";
import { Song } from "@/interfaces/interfaces";
import PrintPreview from "@/components/print-preview.component.vue";
import DarkMode from "@/components/dark-mode.component.vue";
import {debounce} from "@/utils/utils";

const appVersion: Ref<string> = ref(process.env.VUE_APP_APP_VERSION);
const appContact: Ref<string> = ref(process.env.VUE_APP_APP_CONTACT);

const appStateService: AppStateService = inject("appStateService")!;

const text: Ref<string | undefined> = ref();
const song: Reactive<Song> = appStateService.song;
const enableButtons: Ref<boolean> = ref(true);

watch(
  () => song.author,
  (value) => {
    if (value != null) {
      sessionStorage.setItem("author", value);
    }
  },
  { immediate: true }
);

watch(
  () => song.title,
  (value) => {
    if (value != null) {
      sessionStorage.setItem("title", value);
    }
  },
  { immediate: true }
);

watch(text, debounce(debounceText, 350), {immediate: true});

onMounted(() => {
  const lyrics: string | null = sessionStorage.getItem("lyrics");

  if (lyrics !== null) {
    text.value = lyrics;
  }

  const author: string | null = sessionStorage.getItem("author");

  if (author !== null) {
    song.author = author;
  }

  const title: string | null = sessionStorage.getItem("title");

  if (title !== null) {
    song.title = title;
  }

  const isDarkMode: boolean =
      (sessionStorage.getItem("darkMode") ?? "false") === "true";

  appStateService.setDarkMode(isDarkMode);
});

function debounceText(value: string): void {
  if (value != undefined) {
    sessionStorage.setItem("lyrics", value);
  }
  doEnableButtons();
}

function doEnableButtons(): void {
  enableButtons.value = text.value !== song.lyrics.toString();
}

function processText(): void {
  if (text.value !== undefined) {
    song.lyrics = Lyrics.buildFromText(text.value);
    enableButtons.value = false;
  }
}

function toText(): void {
  text.value = song.lyrics.toString();
  enableButtons.value = false;
}

</script>

<style scoped lang="scss">
.home {
  height: 100%;

  .divider {
    border-bottom: 1px solid var(--bs-gray-300);
    margin: 0.5rem 0;
  }

  .app-header {
    display: flex;

    .contacts {
      text-align: start;
      font-size: 0.8rem;
      font-style: italic
    }

    .version {
      font-size: 0.8rem;
      margin-bottom: 0.15rem;
      font-style: italic
    }
  }

  .song-info {
    .label {
      text-align: start;
      width: 5rem;
    }
  }
}
</style>
